<template>
    <header-component title="글쓰기 페이지"></header-component>
    <div>
        <textarea v-model="memo"></textarea>
        <div>
            <button @click="setMode(MODE.LIST)">취소</button>
            <button @click="onAddMemo">저장</button>
        </div>
    </div>
    <footer-component></footer-component>
</template>


<script>
    import {MODE} from "../common.js"
    
    export default {
        data(){
            return {memo : ""}
        },
        props : {
            addMemo : {
                type : Function
            }
        },
        methods : {
            onAddMemo(){
                if(this.memo.length != 0){
                    this.addMemo(this.memo);
                }
                else {
                    alert("내용을 입력해 주세요");
                    return;
                }
                this.setMode(MODE.LIST);
            }
        }
    }
</script>

